as de evaluación que promuevan entornos turísticos seguros, responsables y culturalmente respetuosos. La recolección de datos puede realizarse mediante listas de verificación, entrevistas, observaciones directas y reportes de incidentes, combinadas con herramientas comunitarias de control y autocuidado. Este enfoque mixto permite fortalecer la gestión del riesgo, prevenir incidentes futuros y promover la corresponsabilidad entre operadores, comunidades y visitantes en la construcción de un turismo seguro y sostenible. Para ampliar este conocimiento, a continuación, se presenta la tabla 9:")])]),_c('div',{staticClass:"row justify-content-center",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-9"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema7/tema37-tabla9.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('br'),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"flip-up"}},[_c('p',[_vm._v("A modo de ejemplo, se comparte la evaluación de un recorrido realizado en un resguardo indígena se lleva a cabo mediante una reunión de cierre con el Cabildo o la autoridad tradicional.")]),_c('p',[_vm._v("En este espacio de diálogo intercultural se socializan los resultados de las encuestas aplicadas y se abordan reflexiones colectivas orientadas a fortalecer el respeto y la sostenibilidad del proceso.")])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Entre las preguntas que guían este ejercicio se destacan:")]),_c('div',{staticClass:"col-md-11"},[_c('div',{staticClass:"p"},[_vm._v(" • ¿Hubo comportamientos de los visitantes que alteraran la armonía comunitaria?")]),_c('div',{staticClass:"p"},[_vm._v(" • ¿La actividad turística afectó las dinámicas sociales o espirituales del territorio?")]),_c('div',{staticClass:"p"},[_vm._v(" • ¿La retribución económica fue justa, transparente y distribuida conforme a los acuerdos internos?")]),_c('div',{staticClass:"p"},[_vm._v(" • ¿Se garantizó la protección de los sitios sagrados y de los conocimientos tradicionales?")])])]),_c('br'),_c('div',{staticClass:"row justify-content-center",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema7/tema37-figura14.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('br'),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Esta evaluación conjunta integra indicadores técnicos y percepciones culturales, convirtiéndose en una herramienta genuina de aprendizaje mutuo y de mejora continua del servicio turístico.")]),_c('p',[_vm._v("A través de este proceso, se fortalece la confianza entre comunidades y operadores, y se consolida un modelo de turismo responsable, respetuoso y culturalmente consciente.")])])])
