    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.7.	Evaluación de la experiencia turística según protocolos de seguridad

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La evaluación de la experiencia turística según los protocolos de seguridad constituye una etapa clave para garantizar la mejora continua de los servicios y la protección integral de los usuarios y las comunidades anfitrionas. Este proceso permite verificar el cumplimiento de las medidas preventivas, la eficacia de los planes de emergencia y la coherencia entre las prácticas turísticas y los principios de sostenibilidad y respeto cultural. De acuerdo con la Organización Mundial del Turismo (OMT, 2018), la evaluación debe ser integral y abarcar tanto la percepción del visitante como la gestión técnica del prestador del servicio, incorporando indicadores de seguridad física, emocional y ambiental. Esto implica no solo revisar si se siguieron los protocolos de primeros auxilios o control de riesgos, sino también si los turistas se sintieron seguros, informados y respetuosos de las normas locales.

    .row.justify-content-center(data-aos="fade-left")
      .col-lg-12
        figure
          img(src='@/assets/curso/temas/tema3/tema7/rec29-img.svg', alt='Texto que describa la imagen')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Asimismo, los protocolos del Sistema de Gestión de Seguridad y Salud en el Trabajo (SG-SST) y la Ley 2121 de 2021 (Ley de Guías de Turismo) sirven como marco normativo para la implementación de prácticas de evaluación que promuevan entornos turísticos seguros, responsables y culturalmente respetuosos. La recolección de datos puede realizarse mediante listas de verificación, entrevistas, observaciones directas y reportes de incidentes, combinadas con herramientas comunitarias de control y autocuidado. Este enfoque mixto permite fortalecer la gestión del riesgo, prevenir incidentes futuros y promover la corresponsabilidad entre operadores, comunidades y visitantes en la construcción de un turismo seguro y sostenible. Para ampliar este conocimiento, a continuación, se presenta la tabla 9:

    .row.justify-content-center(data-aos="fade-left")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema3/tema7/tema37-tabla9.svg', alt='Texto que describa la imagen')
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p A modo de ejemplo, se comparte la evaluación de un recorrido realizado en un resguardo indígena se lleva a cabo mediante una reunión de cierre con el Cabildo o la autoridad tradicional.
      p En este espacio de diálogo intercultural se socializan los resultados de las encuestas aplicadas y se abordan reflexiones colectivas orientadas a fortalecer el respeto y la sostenibilidad del proceso.

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Entre las preguntas que guían este ejercicio se destacan:

      .col-md-11
        .p 	• ¿Hubo comportamientos de los visitantes que alteraran la armonía comunitaria?
        .p 	• ¿La actividad turística afectó las dinámicas sociales o espirituales del territorio?
        .p 	• ¿La retribución económica fue justa, transparente y distribuida conforme a los acuerdos internos?
        .p 	• ¿Se garantizó la protección de los sitios sagrados y de los conocimientos tradicionales?

    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-5
        figure
          img(src='@/assets/curso/temas/tema3/tema7/tema37-figura14.svg', alt='Texto que describa la imagen')
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Esta evaluación conjunta integra indicadores técnicos y percepciones culturales, convirtiéndose en una herramienta genuina de aprendizaje mutuo y de mejora continua del servicio turístico.
      p A través de este proceso, se fortalece la confianza entre comunidades y operadores, y se consolida un modelo de turismo responsable, respetuoso y culturalmente consciente.

